<template>
  <div class="tax-page account">
    <h4>帐户设置</h4>
    <div class="content">
      <el-form ref="inputForm" :model="user" :rules="rules" label-width="120px">
        <el-form-item label="帐号名称" prop="name">
          <el-input v-model="user.name" placeholder="请输入帐号名称"></el-input>
          <div class="form-tip">可以填入单位名称，支持中文.</div>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="onSubmit">提交</el-button>
        </el-form-item>
      </el-form>
    </div>
  </div>
</template>

<script>
  import UserApi from '@/api/manage/user';

  export default {
    name: "account",
    data() {
      return {
        user: {},
        rules: {
          name: [
            {required: true, message: '请输入帐号名称', trigger: 'blur'},
          ],
        }
      }
    },
    mounted() {
      this.$emit('layout:load', "account")
      this.loadUser()
    },
    methods: {
      loadUser: function () {
        UserApi.info().then(res => {
          this.user = res.obj;
        })
      },
      onSubmit : function() {
        this.$refs['inputForm'].validate(res => {
          if (res) {
            UserApi.saveUser(this.user).then(res => {
              this.$notify.success("保存帐户成功!");
            });
          } else {
            this.$notify.error("表单错误");
          }
        });
      }
    }
  }
</script>

<style lang="stylus" rel="stylesheet/stylus" scoped>
  @require "../../ui/common/stylus/variable.styl"
</style>